import { Component, OnInit } from '@angular/core';
import { NavController, ActionSheetController, LoadingController, AlertController, ToastController } from '@ionic/angular';
import { AppMin } from "../core"
// import { MyWalletRecordPage } from '../my-wallet-record/my-wallet-record';
import { Router } from '@angular/router';




@Component({
  selector: 'app-mywallet',
  templateUrl: './mywallet.page.html',
  styleUrls: ['./mywallet.page.scss'],
})
export class MywalletPage implements OnInit {
  mymoney = "0";
  loader;
  header_class;
  checkReWalletAcce = false;//是否设置提款账户
  constructor(public navCtrl: NavController, public Router: Router, public appMin: AppMin, public toastCtrl: ToastController, public alertCtrl: AlertController, public actionSheetCtrl: ActionSheetController, public loadingCtrl?: LoadingController) {
    if (this.appMin.GetPhoneMode() == "ios") {
      this.header_class = "header_class";
    }
    else {
      this.header_class = "header_class";
      console.log("判断为安卓系统 设置状态栏样式")
    }





  }


  async send(value, isshow?: boolean) {


    if (isshow) {
      this.loader = await this.loadingCtrl.create({
        message: "加载中...",
        duration: 3000

      });
      this.loader.present();
    }
    this.appMin.post(JSON.parse(value)).subscribe((data) => {
      this.dataHandle(data);
    })


  }

  async tixian() {
    //提现按钮

    if (!this.checkReWalletAcce) {

      const alert = await this.alertCtrl.create({

        message: '还没有设置提款账户。',
        buttons: [
          {
            text: '取消',
            role: 'cancel',
            cssClass: 'secondary',
            handler: (blah) => {

            }
          }, {
            text: '去设置',
            handler: () => {
              this.Router.navigate(['drawing-account'], { queryParams: {} })
            }
          }
        ]
      });

      await alert.present();


      return;

    }
    this.Router.navigate(["mywallet-cash"], { queryParams: { mymoney: this.mymoney } });

  }
  addMywalletPage() {
    //加载用户数据
    let obj = {
      type: "addMywalletPage",
      userId: this.appMin.getUserid()
    }
    this.send(JSON.stringify(obj), false);
  }
  dataHandle(meg: any) {
    if (this.loader) {
      this.loader.dismiss();
    }

    console.log(JSON.stringify(meg));
    if (meg['type'] == 'addMywalletPage_ret') {
      this.mymoney = meg["money"];
      if (meg['ali'] != "") {
        this.checkReWalletAcce = true;
      }
    }

    if (meg['type'] == 'withdrawalsMoney_ret') {
      if (meg['meg'] == '1') {
        //已经申请过了
        console.log('已经申请过了');
        this.presentToast("已经申请过了，请查看申请记录")
      }
      else {
        console.log('申请成功');
        this.presentToast("申请成功,将尽快处理")
      }
    }
  }
  async presentAlert(val) {
    let alert = await this.alertCtrl.create({
      header: '提示',
      subHeader: val,
      buttons: ['确定']
    });
    alert.present();
  }
  async presentToast(val) {
    let toast = await this.toastCtrl.create({
      message: val,
      duration: 2000,
      position: 'top'
    });
    toast.present();
  }
  moer() {
    //跳转记录页面

    //  this.navCtrl.push(MyWalletRecordPage); 
    this.Router.navigate(['mywallet-record'], { queryParams: {} });
    // this.Router.navigate(['/my-order-list'], { queryParams: {} })
  }

  ngOnInit() {

    console.log("ngOnInit")
  }
  ionViewWillEnter() {
    console.log("ionViewWillEnter")
    this.addMywalletPage();
  }

}
